h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("FontFont_FF.Mark.Pro.9b0d5f6c.otf") format("otf");
  font-weight: normal;
  font-style: normal;
}

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, code, del, dfn, em, img, q, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  font-weight: inherit;
  font-style: inherit;
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: 100%;
}

em {
  font-style: italic;
}

strong {
  font-weight: bold;
}

:root {
  color: #fff;
  scroll-behavior: smooth;
  background: #14112c;
  font-family: Inter, sans-serif;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  color: #fff;
  text-decoration: none;
}

.no-scroll {
  max-height: 100vh;
  overflow: hidden;
}

.about-greetings {
  background: linear-gradient(146.86deg, #191536 -.5%, #000 99.5%);
  padding: 147px 0 156px;
}

.about {
  justify-content: end;
  align-items: start;
  gap: 110px;
  margin-bottom: 175px;
  padding: 0 234px;
  display: flex;
}

.about__image-wrapper {
  position: relative;
}

.about__image-wrapper:before {
  content: "1/5";
  color: #05c2df;
  font-size: 14px;
  font-weight: 400;
  position: absolute;
  top: 0;
  left: -23px;
  transform: translateX(-100%);
}

.about__image {
  width: 622px;
  height: 290px;
  margin-bottom: 12px;
}

.about__info {
  max-width: 435px;
}

.about__title {
  margin-bottom: 30px;
  font-size: 36px;
  font-weight: 900;
}

.about__title--blue {
  color: #05c2df;
}

.about__text {
  color: #929292;
  margin-bottom: 55px;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.about__video-link {
  padding-left: 108px;
  font-size: 16px;
  font-weight: 500;
  line-height: 130%;
  position: relative;
}

.about__video-link:before {
  content: "";
  background: url("play-video.fdb135e8.svg") center / contain no-repeat;
  width: 225px;
  height: 66px;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-36%);
}

.switches {
  align-items: center;
  gap: 103px;
  width: 207px;
  font-size: 16px;
  font-weight: 500;
  line-height: 150%;
  display: flex;
  position: relative;
}

.switches:before, .switches:after {
  content: "";
  height: 1px;
  position: absolute;
  bottom: -10px;
}

.switches:before {
  background-color: #05c2df;
  width: 33.33%;
  left: 0;
}

.switches:after {
  background-color: #212121;
  width: 66.67%;
  right: 0;
}

.pagination {
  display: none;
}

.pagination__switch {
  background-color: #484848;
  border-radius: 50%;
  width: 4px;
  height: 4px;
}

.pagination__switch--active {
  background-color: #ababab;
}

.greetings {
  justify-content: start;
  align-items: start;
  gap: 101px;
  padding: 0 256px 0 327px;
  display: flex;
}

.greetings__subtitle {
  color: #05c2df;
  margin-bottom: 15px;
  font-size: 14px;
  font-weight: 400;
  display: block;
}

.greetings__title {
  margin-bottom: 30px;
  font-size: 36px;
  font-weight: 900;
}

.greetings__title--blue {
  color: #05c2df;
}

.greetings__text {
  color: #929292;
  max-width: 532px;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.greetings__print {
  align-items: flex-start;
  display: flex;
}

.greetings__rotating-title {
  writing-mode: vertical-rl;
  color: #05040b;
  -webkit-text-stroke: 1px #05c2df;
  font-size: 64px;
  font-weight: 900;
  line-height: 130%;
}

.greetings__rotating-title:nth-of-type(2) {
  opacity: .5;
}

.greetings__rotating-title:nth-of-type(3) {
  opacity: .2;
}

.greetings__rotating-title:nth-of-type(4) {
  opacity: .1;
}

.greetings__rotating-title:nth-of-type(5) {
  opacity: .05;
}

.greetings__rotating-title:nth-of-type(6) {
  opacity: .03;
}

.greetings__rotating-title:nth-of-type(7) {
  opacity: .02;
}

.greetings__rotating-title:nth-child(2n) {
  transform: rotate(180deg);
}

@media screen and (width >= 1280px) and (width <= 1919px) {
  .about {
    padding: 0 110px 0 246px;
  }

  .about__image {
    aspect-ratio: 430 / 290;
    width: auto;
    height: 290px;
  }

  .switches-left {
    color: #929292;
  }

  .greetings {
    padding: 0 110px;
  }
}

@media screen and (width >= 768px) and (width <= 1279px) {
  .about-greetings {
    padding: 83px 0 50px;
  }

  .about {
    gap: 20px;
    margin-bottom: 100px;
    padding: 0 34px;
  }

  .about__title {
    font-size: 21px;
  }

  .about__image-wrapper:before {
    display: none;
  }

  .about__image {
    aspect-ratio: 340 / 270;
    width: auto;
    height: 270px;
  }

  .switches {
    display: none;
  }

  .pagination {
    justify-content: center;
    align-items: center;
    gap: 6px;
    display: flex;
  }

  .greetings {
    align-items: center;
    padding: 0 34px;
  }

  .greetings__title {
    font-size: 21px;
  }

  .greetings__rotating-title {
    font-size: 48px;
  }

  .greetings__rotating-title:not(:nth-of-type(-n+3)) {
    display: none;
  }
}

@media screen and (width >= 320px) and (width <= 767px) {
  .about-greetings {
    padding: 50px 0;
  }

  .about {
    flex-direction: column;
    gap: 20px;
    margin-bottom: 80px;
    padding: 0 20px;
  }

  .about__title {
    font-size: 21px;
  }

  .about__image-wrapper:before {
    display: none;
  }

  .about__image {
    aspect-ratio: 280 / 190;
    width: 100%;
    height: auto;
  }

  .switches {
    display: none;
  }

  .pagination {
    justify-content: center;
    align-items: center;
    gap: 6px;
    display: flex;
  }

  .greetings {
    align-items: center;
    padding: 0 34px;
  }

  .greetings__info {
    padding-top: 50px;
  }

  .greetings__title {
    font-size: 21px;
  }

  .greetings__print {
    display: none;
  }
}

.contact-form {
  width: 716px;
}

.contact-form__name-wrapper, .contact-form__email-wrapper, .contact-form__phone-wrapper, .contact-form__message-wrapper {
