.header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 230px;
  padding: 60px 233px;
  display: flex;
  position: relative;
}

.header__arrow-right {
  width: 8.5px;
  height: 17px;
  display: none;
}

.header__arrow-right--active {
  z-index: 30;
  display: block;
}

.header__logo-language {
  align-items: center;
  gap: 34px;
  display: flex;
}

.header__language {
  align-items: end;
  gap: 3px;
  display: flex;
}

.header__list {
  align-items: center;
  gap: 32px;
  display: flex;
}

.header__list-item {
  font-size: 16px;
  font-weight: 500;
  line-height: 130%;
}

.header__list-item:nth-of-type(n+6) {
  display: none;
}

.header__list-item:nth-of-type(5) {
  background-color: #05c2df;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 200px;
  padding: 13px 0;
  display: flex;
}

.languages-list {
  display: none;
}

.burger-menu {
  z-index: 20;
  cursor: pointer;
  width: 29.2px;
  height: 16px;
  display: none;
}

.burger-menu__line {
  background-color: #fff;
  height: 1px;
  transition: transform .3s;
  display: block;
}

.burger-menu__line:first-of-type {
  width: 23.8px;
}

.burger-menu__line:nth-of-type(2) {
  width: 17.3px;
  padding-right: 12px;
}

.burger-menu__line:last-of-type {
  width: 23.8px;
}

.burger-menu--active .burger-menu__line:nth-of-type(2) {
  display: none;
}

.burger-menu--active .burger-menu__line:first-of-type {
  transform-origin: center;
  transform: rotate(-45deg)translateY(5px);
}

.burger-menu--active .burger-menu__line:last-of-type {
  transform-origin: center;
  transform: rotate(45deg)translateY(-5px);
}

